body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.app__header {
  padding: 20px;
  text-align: center; }

.header__title {
  margin-top: 0px;
  font-size: 50px; }

.header__filter {
  outline: none;
  text-align: center;
  font-weight: bold; }

.wizards__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px; }

.wizard__item {
  margin: 0;
  background: #800213;
  text-align: center;
  list-style: none;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  border: 3px solid black; }

.wizard__item-name {
  color: white; }

.wizard__item-image {
  width: 160px;
  height: 230px;
  margin: 20px; }

.wizard__item-house {
  color: white;
  -webkit-text-decoration-line: none;
          text-decoration-line: none; }

.wizard__details-container {
  background: #800213;
  text-align: center;
  padding: 0; }

.wizard__details-currentHouse {
  padding-top: 10px;
  border-radius: 50%; }

.wizard__details-name {
  margin: 0;
  padding: 10px; }

.wizard__details-image {
  width: 250px;
  height: 350px; }

.wizard__details-alive {
  margin-bottom: 30px; }

.link__return {
  text-decoration: none;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  background-color: black;
  padding: 10px; }

.app {
  padding: 2em;
  background-color: black;
  color: white; }

@media all and (max-width: 930px) {
  .wizards__list {
    grid-template-columns: repeat(3, 1fr); } }

@media all and (max-width: 800px) {
  .wizards__list {
    grid-template-columns: repeat(2, 1fr); } }

@media all and (max-width: 500px) {
  .wizards__list {
    grid-template-columns: repeat(1, 1fr); } }

@media all and (max-width: 500px) {
  .wizard__details-image {
    width: 200px;
    height: 300px; } }

